.formulario{
    p{
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0;
    }

    legend{
        font-size: 2rem;
        color: $grisOscuro;
    }

    label{
        font-weight: $bold;
        text-transform: uppercase;
        display: block;
    }

    input:not([type = 'submit']), //Se lo aplica a todos los inputs menos al boton de tipo submit ya trabajado previamente con respecto a su codigo CSS
    textarea, select{
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $gris;
        border: 1px solid $gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }
}

textarea{
    height: 30rem;
}

.forma-contacto{
    max-width: 30rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    input[type = 'radio']{
        margin: 0;
        width: auto;
    }
}